.projectsBlock {
   position: relative;
   min-height: 600px;
   padding: 100px 0;
   background-color: #2a2a2a;
}
.projectsContainer {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
.title {
   font-size: 3.2rem;
   color: #ffffff;
   text-transform: uppercase;
   margin-bottom: 30px;
}

.projects{
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   width: 100%;


}
.whiteSvg {
   position: absolute;
   bottom: -3px;
   left: 0;
   width: 100%;
}

.whiteSvg svg {
   fill: rgb(32, 32, 32);
   border-bottom: 1px solid hsla(0, 0%, 40%, .3);
}

@media only screen and (max-width: 800px) {
   .projects{
      justify-content: center;
   }
}