.mainBlock {
    background-color: rgb(32, 32, 32);
    position: relative;
    display: flex;

}

.mainContainer {
    align-items: center;
    min-height: 100vh !important;
}

.greeting {
}

.greeting h2 {
    text-transform: uppercase;
    color: #ffffff;
    letter-spacing: 8px;
    font-weight: 400;
}

.greeting p {
    color: #ffffff;
    text-transform: uppercase;
    font-size: 4rem;
    font-weight: 700;
}

.greeting p:nth-last-child(2) {
    text-indent: 100px;
    margin-bottom: 30px;
}

.photo {
    background-image: url("./../common/images/User1.jpeg");
    background-size: cover;
    width: 300px;
    background-position: center;
    height: 400px;
}


.whiteSvg {
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
}

.whiteSvg svg {
    fill: #2a2a2a;
    border-bottom: 1px solid hsla(0, 0%, 40%, .3);
}