.aboutMe {
    background-color: #2a2a2a;
    position: relative;
}

.aboutMeContainer {
    min-height: 90vh;
    display: flex;
    align-items: center;
}

.border {
    border: none;
    position: absolute;
    left: 50%;
    top: 0;
    width: 1px;
    height: 100%;
    background: hsla(0, 0%, 40%, .3) !important;
    z-index: 0;
}

.aboutItem {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.imageBorder {
    position: relative;
}

.imageBorder:before,
.aboutImage:after {
    content: '';
    position: absolute;
    width: 350px;
    height: 350px;
    top: 20px;
    left: -25px;
    transition: all 0.5s;
}

.imageBorder:before {
    border: 10px solid #ddd;
    transform: rotate(136deg);
}

.imageBorder:hover:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.aboutImage:after {
    border: 10px solid #555;
    -webkit-transform: rotate(-91deg);
    transform: rotate(-91deg);
}

.aboutImage:hover:after {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}

.aboutImage {
    background-image: url("./../common/images/User1.jpeg");
    background-size: cover;
    width: 300px;
    background-position: center;
    height: 400px;
}


.aboutContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 450px;
}

.aboutContent h2 {
    position: relative;
    color: #fff;
    padding: 20px 0;
}

.aboutContent h2:after {
    content: "";
    position: absolute;
    width: 180px;
    height: 5px;
    background: #666;
    top: 80%;
    left: 0;
    bottom: 0;
}

.aboutContent p {
    color: #fff;
    padding: 30px 0;
}

.whiteSvg {
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;

}

.whiteSvg svg {
    fill: #202020;
}