.navbar {
   position: relative;
   justify-content: flex-end;
   align-self: center;
   padding-right: 20px;

}

.button {
   width: 45px;
   height: 45px;
   background-color: #191919;
   border: none;
   padding: 7px;
   display: flex;
   flex-direction: column;
   align-items: end;
   justify-content: space-evenly;
}


.button span {
   border: 1px solid #ffffff;
   width: 33px;
}

.button span:nth-last-child(1){
   width: 20px;
}

.navBlock {
   position: absolute;
   right: 0;
   top: 0;
   width: 500px;
   height: 100vh;
   transform: translate(501px);
   transition: all 1s !important;
}
.navBlockOpen {
   position: absolute;
   right: 0;
   top: 0;
   width: 500px;
   height: 100vh;
   background-color: #191919;
   transform: translate(0px);
   transition: all 1s !important;
   z-index: 999;
}

ul li{
   list-style: none;
   margin: 0 auto;
}

img {
   height: 200px;
   padding-left: 40px;
}

@media only screen and (max-width: 450px) {
   .nav{
      width: 250px;
   }
}


.test {
   position: absolute;
   width: 1000px;
   height: 100vh;
   background: red;
   top: 0;
   right: 0;
   opacity: 10%;
   z-index: -999;
}